import React from 'react';

class OfferContent extends React.Component {
render() {
    return (
        <div className="oferta">
          <p>
            Dzieci w przedszkolu w radosnej atmosferze nawizuj kontakty i przyjanie. Wpywa to korzystnie na rozwj zachowa spoecznych takich jak: tolerancja, ustpowanie, pomoc, cierpliwo, odwaga.
          </p>
          <ul><u>NASZE PRZEDSZKOLE OFERUJE:</u> 
            <li>kontakty ze rodowiskiem</li>
            <li>poznawanie przyrody</li>
            <li>spotkania z ciekawymi ludmi</li>
            <li>imprezy kulturalne: kino, teatr,
            warsztaty teatralne</li>
            <li>comiesiczne koncerty muzykw z Filharmonii Jeleniogrskiej na miejscu w przedszkolu</li>
            <li>Bal Przebieracw, Dzie Dziecka, wito Babci i Dziadka</li>
            <li>bezpatnie rytmik, warsztaty plastyczne, zabawy ruchowe</li>
            <li>j. angielski w podstawie programowej obejmuje dzieci od grupy najmodszej</li>
            <li>dodatkowa oferta za opata to j. niemiecki</li>
          </ul>
          <p>
            Jestemy jedynym przedszkolem na terenie Zgorzelca, ktre od wielu lat systematycznie wsppracuje z przedszkolem Zwergenhaus w Goerlitz. W ramach tej wsppracy oferujemy dla dzieci wycieczki do Teatru w Goerliz, Muzeum Przyrodniczego, Pracowni Ceramiki, ZOO, wycieczki autokarowe do parku Dinozaurw w Bautzen (wymagany jest paszport dziecka). 
          </p>
          <p>
            Czynnie uczestniczymy w imprezach na terenie miasta. 
          </p>
          <p>
            Przedszkole czynne jest w godz. 6:30-16:30. 
          </p>
          <p>
            Przyjmujemy dzieci do grup rozwojowych: trzy i czterolatki, piciolatki, szeciolatki. Dzieci znajduj si pod opiek wykwalifikowanej kadry pedagogicznej. Jestemy otwarci na propozycje rodzicw.
          </p>
        </div>
    );
}
}
export default OfferContent;

